
* {
  margin: 0
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.menu-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;

  margin: .4em;
}
.script-link {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;

  width: 10em;
  height: 3.3em;
  margin: 0.6em;

  cursor: pointer;
  font-size: 1rem;
  border-radius: .16em;
  -webkit-transition: 150ms all;
  transition: 150ms all
}
.script-link:hover {
  background-color: #ffffffbb;
  -webkit-box-shadow: 0 8px 16px 0 #0000001a;
          box-shadow: 0 8px 16px 0 #0000001a;
}
.script-link:active {
  -webkit-box-shadow: 0 1px 2px 0 #0000001a;
          box-shadow: 0 1px 2px 0 #0000001a;
}
a {
  text-decoration: none;
  color: #2c3e50;
}
